html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-Nunito);
}

:root {
  --font-Nunito: "Nunito", sans-serif;
}

ul.toggle-list,
li.toggle-list-item {
  list-style: none;
  margin: 0;
  padding: 0;
}

.toggle-list {
  text-align: center;
  align-items: center;
  width: 5%;
  position: fixed;
  top: 1rem;
  right: 5rem;
}
.toggle-list-item {
  margin: 0 2em;
}
.toggle-list-item > h4 {
  color: rgba(0, 0, 0, 0.4);
  margin-bottom: 0.3rem;
}

.toggle {
  display: none;
}
.toggle, .toggle:after, .toggle:before, .toggle *, .toggle *:after, .toggle *:before, .toggle + .toggle-btn {
  box-sizing: border-box;
}
.toggle::selection, .toggle:after::selection, .toggle:before::selection, .toggle *::selection, .toggle *:after::selection, .toggle *:before::selection, .toggle + .toggle-btn::selection {
  background: none;
}
.toggle + .toggle-btn {
  outline: 0;
  display: block;
  width: 4em;
  height: 2em;
  margin: 0 auto;
  position: relative;
  cursor: pointer;
  user-select: none;
}
.toggle + .toggle-btn:after, .toggle + .toggle-btn:before {
  position: relative;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
}
.toggle + .toggle-btn:after {
  left: 0;
}
.toggle + .toggle-btn:before {
  display: none;
}
.toggle:checked + .toggle-btn:after {
  left: 50%;
}
.toggle.toggle-skewed + .toggle-btn {
  overflow: hidden;
  transform: skew(-10deg);
  backface-visibility: hidden;
  transition: all 0.2s ease;
  font-family: sans-serif;
  background: #878787;
}
.toggle.toggle-skewed + .toggle-btn:after, .toggle.toggle-skewed + .toggle-btn:before {
  transform: skew(10deg);
  display: inline-block;
  transition: all 0.2s ease;
  width: 100%;
  text-align: center;
  position: absolute;
  line-height: 2em;
  font-weight: bold;
  color: white;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
}
.toggle.toggle-skewed + .toggle-btn:after {
  left: 100%;
  content: attr(data-toggle-on);
}
.toggle.toggle-skewed + .toggle-btn:before {
  left: 0;
  content: attr(data-toggle-off);
}
.toggle.toggle-skewed + .toggle-btn:active {
  background: #878787;
}
.toggle.toggle-skewed + .toggle-btn:active:before {
  left: -10%;
}
.toggle.toggle-skewed:checked + .toggle-btn {
  background: #87d993;
}
.toggle.toggle-skewed:checked + .toggle-btn:before {
  left: -100%;
}
.toggle.toggle-skewed:checked + .toggle-btn:after {
  left: 0;
}
.toggle.toggle-skewed:checked + .toggle-btn:active:after {
  left: 10%;
}